import React, { Component } from 'react';
import Table from 'react-bootstrap/Table';
import Plot from 'react-plotly.js';
import { findCostPer } from 'src/Utils/Utils';

class Playground extends Component {

    //default
    constructor(props) {
        super(props);
        this.state = {
            categoryListData: [],
            categoryPaymentHistoryData:[]
        };
    }

    componentDidMount() {
        document.title = "Playground page";
    }

    testRenderingLogic() {
        const categoryPaymentHistory = {
            category: {	
                id : 93,
                name: 'paneer - cottage cheese',
                unit_of_measurement: 'GRAM' // GRAM, COUNT, ML
            },
            products : [
                { 
                    id : 485,
                    name: 'Mother Dairy Paneer',
                    brand : {id : 23, name : 'Mother Dairy'},
                },
                { 
                    id : 345, 
                    name: 'Amul malai paneer', 
                    brand : {id : 465, name : 'miklymist'} 
                }
            ],
            priceMatrix : [
                { productId : 485, quantity : 400, amount: 100, expenseDate : '2023-11-10 15:04'},
                { productId : 485, quantity : 500, amount: 500, expenseDate : '2023-11-12 15:04'},
                { productId : 345, quantity : 450, amount: 500, expenseDate : '2023-11-15 15:04'},
                { productId : 345, quantity : 450, amount: 400, expenseDate : '2023-11-20 15:04'},
                { productId : 345, quantity : 450, amount: 700, expenseDate : '2023-12-15 15:04'},
                { productId : 485, quantity : 500, amount: 400, expenseDate : '2023-12-23 15:04'},
                { productId : 345, quantity : 450, amount: 100, expenseDate : '2023-12-25 15:04'},
            ]
        };

        let x = [] , y = [];
        categoryPaymentHistory
            .priceMatrix
            .forEach(paymentHistory => {
                x.push(paymentHistory.expenseDate);
                y.push(findCostPer(paymentHistory.quantity, paymentHistory.amount, 100)); //cost per 100gm
            });

        return [
            {
                x: [ ...x ],
                y: [ ...y ],
                type: 'scatter',
                mode: 'lines+markers',
                marker: {color: 'red'},
                name: `price`
            }
        ];
    }

    testRenderingLogicV2() {
        const categoryPaymentHistory = {
            category: {	
                id : 93,
                name: 'paneer - cottage cheese',
                unit_of_measurement: 'GRAM' // GRAM, COUNT, ML
            },
            products : [
                { 
                    id : 485,
                    name: 'Mother Dairy Paneer',
                    brand : {id : 23, name : 'Mother Dairy'},
                },
                { 
                    id : 345, 
                    name: 'Amul malai paneer', 
                    brand : {id : 465, name : 'miklymist'} 
                }
            ],
            priceMatrix : [
                { productId : 485, quantity : 400, amount: 100, expenseDate : '2023-11-10 15:04'},
                { productId : 485, quantity : 500, amount: 500, expenseDate : '2023-11-12 15:04'},
                { productId : 345, quantity : 450, amount: 500, expenseDate : '2023-11-15 15:04'},
                { productId : 345, quantity : 450, amount: 400, expenseDate : '2023-11-20 15:04'},
                { productId : 345, quantity : 450, amount: 700, expenseDate : '2023-12-15 15:04'},
                { productId : 485, quantity : 500, amount: 400, expenseDate : '2023-12-23 15:04'},
                { productId : 345, quantity : 450, amount: 100, expenseDate : '2023-12-25 15:04'},
            ]
        };

        const plotlyFinalData = [];
        const COLORS = [
            "red","green","blue","#b59a14","#b514a5","black","orange"
          ];
        
        categoryPaymentHistory
            .products
            .forEach((product, index) => {
                let x = [] , y = [];

                categoryPaymentHistory
                    .priceMatrix
                    .forEach(paymentHistory => {
                        if(paymentHistory.productId === product.id) {
                            x.push(paymentHistory.expenseDate);
                            y.push(this.findCostPer(paymentHistory.quantity, paymentHistory.amount, 100)); //cost per 100gm
                        }
                    });

                plotlyFinalData.push(
                    {
                        x: [ ...x ],
                        y: [ ...y ],
                        type: 'scatter',
                        mode: 'lines+markers',
                        marker: {color: COLORS[index]},
                        name: `price`
                    }
                );

            });

        return plotlyFinalData;
    }

    render() {
        const width = window.innerWidth * 0.85;
        const height = window.innerHeight * 0.60;

        return (
            <div>
                <p>Hello from Playground !!!</p>
                <Plot
                    showlegend={false}
                    data={ this.testRenderingLogicV2() }
                    layout={ {width, height, title: 'Weight data' } }
                />

                <Plot
                    data={[
                    {
                        x: [1, 2, 3],
                        y: [2, 6, 3],
                        type: 'scatter',
                        mode: 'lines+markers',
                        marker: {color: 'red'},
                    },
                    //{ x: [1, 2, 3], y: [2, 5, 3], type: 'bar'},
                    ]}
                    layout={ {width, height, title: 'Weight data' } }
                />

                    <Table striped bordered hover color='success' style={{'width':'100%'}}>
                        <thead>
                            <tr>
                            <th>#</th>
                            <th>name</th>
                            <th>unitOfMeasurement</th>
                            <th>metadata</th>
                            
                            <th>createdAt</th>
                            <th>updatedAt</th>

                            <th>Actions</th>
                            </tr>
                        </thead>
                        <tbody>
                            {
                                this.state.categoryListData.map(
                                    category => 
                                    <tr key={"category_item_id_" + category.id}>
                                        <td>{category.id}</td>
                                        <td>{category.name}</td>
                                        <td>{category.unitOfMeasurement}</td>
                                        <td>{JSON.stringify(category.metadata)}</td>
                                        <td>
                                            UPDATE
                                        </td>
                                    </tr>
                                )
                            }
                        </tbody>
                    </Table>
            </div>
        )
    }

}

export default Playground;